// import pagetitle from '@/components/title'
export default {
    // components: {
    //     pagetitle
    // },
    data(){
        return {
            show: false, // 遮罩层
            wl_show: false, // 物流选择
            checked: true,
            checked1: true,
            checked2: true,
            checked3: true,
            wl_list: [
                {name:'顺丰',price:'免邮',index:0},
                {name:'中通',price:'免邮',index:1},
                {name:'圆通',price:'￥20',index:2}
            ],
        }
    },
    methods: {
        // 选择运费
        changeShip(){
            this.show = true;
            this.wl_show = true;
        },
        // 点击遮罩
        showOverlay(){
            this.show = false;
            this.wl_show = false;
        },
        toggle(index) {
            this.$refs.checkboxes[index].toggle();
        },
		switch2Change(){
			
		},
		switch1Change(){
			
		}
    }
}
